


























































































































import Vue from "vue";
import qs from "qs";
import axios from "axios";
import { mapActions, mapGetters } from "vuex";
export default Vue.extend({
  data() {
    return {
      user: "yunlindemo",
      password: "Demo1234",
      msg: "",
      logining: false
    };
  },
  computed: {},
  methods: {
    ...mapActions(["setToken"]),
    login() {
      this.logining = true;
      let param = {
        grant_type: "password",
        username: this.user,
        password: this.password
      };

      axios
        .post("/token", qs.stringify(param))
        .then(res => {
          const ret = res.data;
          if (res.status === 200) {
            this.setToken(ret.access_token);
          }
          this.logining = false;
        })
        .catch(err => {
          this.logining = false;
          this.msg = "帳號或密碼錯誤!";
          this.$bvModal.show("my-modal");
        });
    }
  }
});
