<template>
  <div id="OMap" style="width: 100%;height: 100%;"></div>
</template>
<style scoped>
</style>

<script>
import proj4 from "proj4";

//------------------------------須自行修改的參數,包含點位坐標,訊息視窗內容及圖示檔案來源設定------------------------------
proj4.defs([
  [
    "EPSG:4326",
    "+title=WGS 84 (long/lat) +proj=longlat +ellps=WGS84 +datum=WGS84 +units=degrees"
  ],
  [
    "EPSG:3826",
    "+title=TWD97 TM2+proj=tmerc +lat_0=0 +lon_0=121 +k=0.9999 +x_0=250000 +y_0=0 +ellps=GRS80 +units=公尺 +no_defs"
  ],
  [
    "EPSG:3828",
    "+proj=tmerc +lat_0=0 +lon_0=121 +k=0.9999 +x_0=250000 +y_0=0 +ellps=aust_SA"
  ]
]);
const EPSG3826 = new proj4.Proj("EPSG:3826");
//------------------------------若網頁介面依照範例網頁的預設設定,以下程式碼可不修改---------------------------------------

import { mapActions, mapState } from "vuex";
export default {
  name: "tgosMap",
  mounted() {
    this.initTGOS();
    this.fetchRainStation();
  },
  data() {
    return {
      pOMap: undefined,
      pMap: undefined,
      activeMarker: []
    };
  },
  computed: {
    ...mapState(["activeMap"]),
    ...mapState("rain", {
      rainStations: state => state.stations
    })
  },
  watch: {
    activeMap(newValue, oldValue) {
      if (newValue === 0) {
        // 氣象圖
      } else if (newValue === 1) {
        this.fetchRainStation();
      } else if (newValue === 2) {
        // 水位
      } else if (newValue === 3) {
        // 淹水感測器
      } else if (newValue === 4) {
        // 即時監控
      } else if (newValue === 5) {
        // 防汛工具
      }
    },
    rainStations(newValue, oldValue) {
      if (this.activeMap === 1) this.prepareRainStationMarker();
    }
  },
  methods: {
    ...mapActions("rain", ["fetchRainStation"]),
    initTGOS() {
      //------------------初始化地圖--------------------
      this.pOMap = document.getElementById("OMap");
      let mapOptiions = {
        /*
        mapTypeControl: true, 
        mapTypeControlOptions: {
          mapTypeIds: [
            TGOS.TGMapTypeId.TGOSMAP,
            TGOS.TGMapTypeId.NLSCMAP,
            TGOS.TGMapTypeId.ROADMAP
          ],
          controlPosition: TGOS.TGControlPosition.RIGHT_TOP,
          mapTypeControlStyle: TGOS.TGMapTypeControlStyle.DEFAULT
          //mapTypeControlStyle(設定地圖類型控制項樣式,預設 / 下拉式選單 / 水平)
        }, */
        navigationControl: false,
        navigationControlOptions: {
          controlPosition: TGOS.TGControlPosition.LEFT_BOTTOM, //控制項位置
          navigationControlStyle: TGOS.TGNavigationControlStyle.SMALL //控制項樣式
        },
        scaleControl: true
      };

      this.pMap = new TGOS.TGOnlineMap(
        this.pOMap,
        TGOS.TGCoordSys.EPSG3826,
        mapOptiions
      ); //建立地圖,選擇TWD97坐標

      let twd97 = proj4(EPSG3826, [120.52636, 23.699594]);
      this.pMap.setZoom(7); //初始地圖縮放層級
      this.pMap.setCenter(new TGOS.TGPoint(twd97[0], twd97[1])); //初始地圖中心點
      //------------------建立標記點---------------------
    },
    clearActiveMarker() {
      for (let marker of this.activeMarker) {
        marker.setMap(null);
      }
      this.activeMarker.splice(0, this.activeMarker.length);
    },
    prepareRainStationMarker() {
      this.clearActiveMarker();
      const imgUrl =
        "http://icons.iconarchive.com/icons/google/noto-emoji-travel-places/24/42686-umbrella-with-rain-drops-icon.png"; //標記點圖示來源

      let markerImg = new TGOS.TGImage(
        imgUrl,
        new TGOS.TGSize(38, 33),
        new TGOS.TGPoint(0, 0),
        new TGOS.TGPoint(10, 33)
      ); //設定標記點圖片及尺寸大小

      let InfoWindowOptions = {
        maxWidth: 4000, //訊息視窗的最大寬度
        pixelOffset: new TGOS.TGSize(5, -30), //InfoWindow起始位置的偏移量, 使用TGSize設定, 向右X為正, 向上Y為負
        zIndex: 99 //視窗堆疊順序
      };
      let pMap = this.pMap;
      for (let station of this.rainStations) {
        let log = parseFloat(station.Location.Longitude);
        let lat = parseFloat(station.Location.Latitude);
        let twd97 = proj4(EPSG3826, [log, lat]);
        let point = new TGOS.TGPoint(twd97[0], twd97[1]);
        let pTGMarker = new TGOS.TGMarker(pMap, point, station.Name, markerImg); //建立機關單位標記點

        //測站名稱、站碼、行政區、過去累積10min、1hr、3hr、6hr、12hr、24hr數值、權責單位、資料時間
        let context = `
        <div class="text-left">
          <strong>測站名稱:</strong>${station.Name}<br/>
          <strong>站碼:</strong>${station.Code}<br/>
          <strong>行政區:</strong>${station.District}<br/>
          <strong>過去累積10分鐘:</strong>${station.AccRain.Acc10min}<br/>
          <strong>過去累積1小時:</strong>${station.AccRain.Acc1h}<br/>
          <strong>過去累積3小時:</strong>${station.AccRain.Acc3h}<br/>
          <strong>過去累積6小時:</strong>${station.AccRain.Acc6h}<br/> 
          <strong>過去累積12小時:</strong>${station.AccRain.Acc12h}<br/> 
          <strong>過去累積24小時:</strong>${station.AccRain.Acc24h}<br/> 
          <strong>權責單位:</strong>${station.Owner}<br/> 
          <strong>資料時間:</strong>${station.Timestamp}<br/> 
        </div>
        `;
        let messageBox = new TGOS.TGInfoWindow(
          context,
          point,
          InfoWindowOptions
        ); //建立訊息視窗

        let openInfoWindow = () => messageBox.open(pMap);
        let closeInfoWindow = () => messageBox.close();

        TGOS.TGEvent.addListener(pTGMarker, "mouseover", openInfoWindow); //滑鼠監聽事件--開啟訊息視窗
        TGOS.TGEvent.addListener(pTGMarker, "mouseout", closeInfoWindow); //滑鼠監聽事件--關閉訊息視窗
        this.activeMarker.push(pTGMarker);
      }
    }
  }
};
</script>