// profile/mutations.ts
import { MutationTree } from 'vuex';
import { RainState, RainStation } from './types';

export const mutations: MutationTree<RainState> = {
    loadStation(state, payload: Array<RainStation>) {
        state.stations.splice(0, state.stations.length)
        for (let station of payload) {
            state.stations.push(station)
        }
    },
    showStation(state, show: boolean) {
        state.show = show
    }
};