// profile/actions.ts
import { ActionTree, mapState } from 'vuex';
import axios from 'axios';
import { RainState, RainStation } from './types';
import { RootState } from '../types';

export const actions: ActionTree<RainState, RootState> = {
    fetchRainStation({ commit }): any {
        axios.get("/station/rain").then((response) => {
            const payload: Array<RainStation> = response && response.data;
            commit('loadStation', payload);
        }).catch(err => alert(err))
    },
    setShow({ commit }, show: boolean) {
        commit("showStation", show);
    }
};