import Vue from 'vue'
import Vuex, { StoreOptions } from 'vuex'
import axios from 'axios'
import { RootState } from './types'
import { profile } from './profile/index'
import { rain } from './rain/index'
import qs from 'qs'

Vue.use(Vuex)

const store: StoreOptions<RootState> = {
  state: {
    token: "",
    activeMap: 0
  },
  actions: {
    setToken({ commit }, token: string) {
      let bearerToken = `Bearer ${token}`;
      axios.defaults.headers.common['Authorization'] = bearerToken;
      commit('updateToken', bearerToken);
    },
    setActiveMap({ commit }, mapNo: number) {
      commit('updateActiveMap', mapNo);
    }
  },
  getters: {
    isLogin(state): boolean {
      return state.token.length != 0
    }
  },
  mutations: {
    updateToken(state, token: string) {
      state.token = token
    },
    updateActiveMap(state, mapNo: number) {
      state.activeMap = mapNo
    }
  },
  modules: {
    profile,
    rain
  }
};
export default new Vuex.Store<RootState>(store);